@use 'utilities' as *;

/* 
POST PREVIEW CARDS
Layout and styling for articles post cards.
*/

article {
    position: relative;
    display: flex;
    flex-direction: column;

    a:has(img) {
        display: block;
    }

    footer {
        display: flex;
        flex-direction: column;
        gap: var(--space-2xs);
        color: var(--clr-text-900);
    }
}

.post-thumb-img {
    width: 100%;
    height: auto;
    aspect-ratio: 16 / 9;
    object-fit: cover;
}

